                        <span style="color: rgb(0,51,102)">
                            <strong>{{tab.title}}</strong>
                        </span>
                    </heading>
                    <div>
                        <strong>{{tab.position}}</strong>
                    </div>
                    <div>{{tab.body}}</div>
                    <div>{{tab.demand}}</div>
                    <p>021-31185118</p>
                    <p>&nbsp;</p>

                </div>
                <!-- spost -->
            </div>
            <!-- div-left -->
            <!-- div-left -->

            <div>
                <span>
                    <span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>www.dataware-system.com</span>
            </div>
            <div>
                <span>
                    <span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;hrnpmdev</span>qbm@dataware-system.com</span>
            </div>
            <div>
                <span>
                    <span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>021-31185118</span>
            </div>
            <div>
                <span>
                    <span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>021-31185119</span>
            </div>
