<script>
import heading from "../components/heading";
export default {
    components: {
        heading,
    },
    data() {
        return {
            tabs: [
                {
                    title: "",
                    body: "-PPT",
                    position: "",
                    demand: ""

                }, {
                    title: "ui",
                    body: "-PPT",
                    position: "",
                    demand: ""

                }, {
                    title: "",
                    body: "-PPT",
                    position: "",
                    demand: ""

                }, {
                    title: "web",
                    body: "-PPT",
                    position: "",
                    demand: ""

                }]
        }
    }

}
</script>

<style>
.div-left {
    float: left;
}

.contact div.spost {
    line-height: 20px;
    width: 450px;
    padding: 0 25px;
}

.contact :hover.spost {
    background-color: #e3e3e3;
}

.contact {

    width: 1000px;
    margin: auto;
}

.contact div {
    font-size: 14px;
    line-height: 30px;
}

.w-min {
    background: #e5e5e5;
    float: left;
    margin-right: 20px;
}
</style>




